require('normalize.css/normalize.css');
require('styles/Chart.css');

import React from 'react';

// Echarts
//  ECharts 
let echarts = require('echarts/lib/echarts');
// 
require('echarts/lib/chart/line');
require('echarts/lib/chart/bar');
// 
require('echarts/lib/component/title');
require('echarts/lib/component/legend');
require('echarts/lib/component/tooltip');
require('echarts/lib/component/toolbox');

let detailImage = require('../../images/detail.ico');

// api
let api = require('../../common/config').api;

class Chart extends React.Component {
	render() {
		return (
			<div className="chart-container" ref="chart"></div>
		);
	}
	componentDidUpdate() {
		// let series = this.props.data.series;
		let series = [{
			name: 'PC',
			type: 'line',
			stack: '',
			areaStyle: {
				normal: {}
			},
			data: [120, 132, 101, 134, 90, 230, 210]
		}, {
			name: '',
			type: 'line',
			stack: '',
			areaStyle: {
				normal: {}
			},
			data: [150, 232, 201, 154, 190, 330, 410]
		}];
		let xAxis = [{
			type: 'category',
			boundaryGap: false,
			data: ['', '', '', '', '', '', '']
		}];
		this.option.baseOption.series = series;
		this.option.baseOption.xAxis = xAxis;
		this.myChart.clear();
		this.myChart.hideLoading();
		this.myChart.setOption(this.option);
	}
	componentDidMount() {
		let main = this.refs.chart;
		let series = this.props.data.series;
		let xAxis = this.props.data.xAxis;

		this.myChart = echarts.init(main);
		
		this.option = {
			baseOption: {
				title: {
					text: ''
				},
				tooltip: {
					trigger: 'axis'
				},
				legend: {
					data: ['PC', '']
				},
				toolbox: {
					itemSize: 24,
					feature: {
						// saveAsImage: {},
						myTool: {
							show: true,
							title: '',
							icon: 'image://' + detailImage,
							onclick: function() {
								location.href = '/#/detail';
							}
						}
					}
				},
				grid: {
					left: '3%',
					right: '4%',
					bottom: '3%',
					containLabel: true
				},
				xAxis: xAxis,
				yAxis: [{
					type: 'value'
				}],
				series: series
			},
			media: [{
				query: {
					maxAspectRatio: 1 // 1
				},
				option: {
					legend: { // legend 
						right: 'center',
						top: 100,
						orient: 'vertical' // legend 
					},
					series: [ // 
						{
							radius: [20, '50%'],
							center: ['50%', '30%']
						}, {
							radius: [30, '50%'],
							center: ['50%', '70%']
						}
					]
				}
			}]
		};

		this.myChart.showLoading();
		this.myChart.setOption(this.option);
		window.onresize = function() {
			this.myChart.resize();
		}.bind(this);
	}
}

Chart.defaultProps = {
};

export default Chart;



